import React, { memo } from 'react';

import YRadioCategory from './c-cpns/radio-category';
import YRadioRecommend from './c-cpns/radio-recommend';
import YRadioRanking from './c-cpns/radio-ranking';
import {
  DjRadioWrapper
} from "./style";

export default memo(function YDjradio() {
  return (
    <DjRadioWrapper className="wrap-v2">
      <YRadioCategory />
      <YRadioRecommend />
      <YRadioRanking />
    </DjRadioWrapper>
  )
})
